import { Component, createRef } from 'react';
import { Redirect } from 'react-router-dom';
import { Input, Button, SideBar, ChatItem } from 'react-chat-elements';
import 'react-chat-elements/dist/main.css';
import profileImage from './profile.png';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMicrophone, faPaperPlane } from '@fortawesome/free-solid-svg-icons'

import io from 'socket.io-client';
import generate from 'canihazusername';

export default class Home extends Component {
  state = {
    socket: null,
    messages: [],
    inputRef: createRef(),
    invalidUsername: false,
    isTyping: false,
    members: [],
    roomName: "unknown",
    admin: ""
  }

  componentDidMount() {
    const socket = io("wss://sniper.skydonald.com:8080", {
      transports: ["websocket"],
      autoConnect: false,
      secure: true
    }).on('connect', () => {
      socket.emit('login', this.props.username);
    });

    this.setState({ socket });
    socket.open();

    socket.on('message', (message, author) => {
      const messages = this.state.messages.slice();
      messages.push({
        author,
        message,
        date: new Date()
      });
      this.setState({ messages });
      const elem = document.querySelector('.content');
      elem.scrollTop = elem.scrollHeight;
    });

    socket.on('user join', (username) => {
      const messages = this.state.messages.slice();
      messages.push({
        author: "SkyChat",
        message: `${username} joined the room`,
        date: new Date()
      });
      const members = this.state.members.slice();
      members.push(username);
      this.setState({ messages, members });
      const elem = document.querySelector('.content');
      elem.scrollTop = elem.scrollHeight;
    });

    socket.on('user leave', (username) => {
      const messages = this.state.messages.slice();
      messages.push({
        author: "SkyChat",
        message: `${username} left the room`,
        date: new Date()
      });
      const members = this.state.members.slice();
      members.splice(members.indexOf(username), 1);
      this.setState({ messages, members });
      const elem = document.querySelector('.content');
      elem.scrollTop = elem.scrollHeight;
    });

    socket.on('typing', (username, isTyping) => {
      if (isTyping) {
        this.setState({ isTyping: true });
        // eslint-disable-next-line
        document.querySelector('.typing').innerHTML = `${username} is typing...`;
      } else {
        this.setState({ isTyping: false });
      }
    });

    socket.on('room info', (roomName, admin, members) => {
      this.setState({ roomName, admin, members });
    });

    socket.on('room reset', () => {
      this.setState({ messages: [], roomName: 'unknown', admin: '', members: [] });
    });

    socket.on('ban', (roomName) => {
      alert('You got banned from ' + roomName);
    });

    socket.on('kick', (roomName) => {
      alert('You got kicked from ' + roomName);
    });

    socket.on('user kick', (username) => {
      const messages = this.state.messages.slice();
      messages.push({
        author: "SkyChat",
        message: `${username} got kicked`,
        date: new Date()
      });
      const members = this.state.members.slice();
      members.splice(members.indexOf(username), 1);
      this.setState({ messages, members });
      const elem = document.querySelector('.content');
      elem.scrollTop = elem.scrollHeight;
    });

    socket.on('user ban', (username) => {
      const messages = this.state.messages.slice();
      messages.push({
        author: "SkyChat",
        message: `${username} got banned`,
        date: new Date()
      });
      const members = this.state.members.slice();
      members.splice(members.indexOf(username), 1);
      this.setState({ messages, members });
      const elem = document.querySelector('.content');
      elem.scrollTop = elem.scrollHeight;
    });

    socket.on('invalid username', () => {
      this.setState({ invalidUsername: true });
      alert('Invalid username (already taken)');
    });
  }

  componentWillUnmount() {
    if (this.state.socket) {
      this.state.socket.close();
    }
    this.setState({
      messages: [],
      socket: null,
      inputRef: null
    });
  }

  sendMessage = (e) => {
    e.preventDefault();
    this.state.socket.emit('message', this.state.inputRef.current.input.value);
    // eslint-disable-next-line
    this.state.inputRef.current.input.value = '';
  }

  recordVoiceMessage = (e) => {
    e.preventDefault();
    if (!this.state.isHovering) {
      return this.sendMessage(e);
    }
    navigator.mediaDevices.getUserMedia(
      { audio: true, video: false }
    );
    alert("Doesn't work yet :(");
  }

  sendTyping = (e) => {
    if (e.target.value === '') {
      this.state.socket.emit('typing', false);
      this.setState({ isTyping: false });
      return;
    }
    this.state.socket.emit('typing', true);
    this.setState({ isTyping: true });
  }

  onMouseHover = (isHovering) => {
    this.setState({ isHovering });
  }

  render() {
    if (!this.props.loggedIn || !this.props.username) {
      return (
        <Redirect to="/skychat/" />
      );
    }

    if (this.state.invalidUsername) {
      return (
        <Redirect to="/skychat/" />
      );
    }

    return (
      <div className="chats-page">
        <div className="nav-bar">
          <div className="logo-tab">SkyChat</div>
          <div onClick={this.props.logOut} className="logout-tab">
            Logout
          </div>
        </div>
        {/* messages */}
        <div style={{ width: '20%', float: 'left' }}>
          <SideBar
            center={(
              <div>
                <span style={{ color: 'white', marginBottom: '1rem' }}>
                  Current Room: {this.state.roomName}
                </span>
                <Button text="Create a new room" onClick={() => this.state.socket.emit('create room', generate('{shape} {city_states}'))} />
              </div>
            )}
            bottom={(
              <div style={{ display: 'flex', flexDirection: 'row' }}>
                <input type="text" id="join-room" />
                <button className="join-room" onClick={() => this.state.socket.emit('join room', document.querySelector('#join-room').value)}>Join Room</button>
              </div>
            )}
            type='light' />
            <h3 style={{ marginLeft: '1rem' }}>Online users ({this.state.members.length}):</h3>
            <ul style={{ marginLeft: '1rem' }}>
              {this.state.members.map(member => {
                return (
                  <li key={generate()}>
                    {member}{member === this.state.admin ? ' - Admin' : ''}{this.props.username === this.state.admin && member !== this.state.admin ? (
                      <div>
                        <button onClick={() => this.state.socket.emit('ban', member)}>ban</button>
                        <button onClick={() => this.state.socket.emit('kick', member)}>kick</button>
                      </div>
                    ) : ''}
                  </li>
                );
              })}
            </ul>
        </div>
        <div className="container">
          <div className="content">
            {this.state.messages.map(message => (
                <ChatItem
                  avatar={profileImage}
                  alt={message.author}
                  title={message.author}
                  subtitle={message.message}
                  date={message.date}
                  unread={0}
                  key={generate()} />
              ))}
          </div>
        </div>
        <div className="form-input-1">
          <span className="typing" style={{ display: this.state.isTyping ? 'block' : 'none' }}>SkyChat is typing...</span>
          <form>
            <Input
              placeholder="Type your message here..."
              multiline={false}
              inputStyle={{ backgroundColor: '#282C34', marginTop: this.state.isTyping ? 0 : 21 }}
              ref={this.state.inputRef}
              onChange={this.sendTyping}
              rightButtons={(
                <div className="rce-input-buttons" style={{ marginTop: this.state.isTyping ? 0 : 21 }}>
                  <button className="rce-button" style={{ color: 'white', backgroundColor: '#1D53AA' }} onClick={this.sendMessage}>
                    <FontAwesomeIcon icon={faPaperPlane} style={{ marginRight: '3px' }} /> Send
                  </button>
                </div>
              )}
              leftButtons={(
                <div className="rce-input-buttons" style={{ marginTop: this.state.isTyping ? 0 : 21 }}>
                  <button className="rce-button" style={{ color: 'white', backgroundColor: '#1D53AA' }} onClick={this.recordVoiceMessage} onMouseEnter={() => this.onMouseHover(true)} onMouseLeave={() => this.onMouseHover(false)}>
                    <FontAwesomeIcon icon={faMicrophone} style={{ marginRight: '3px' }} /> Record a voice message
                  </button>
                </div>
              )} />
          </form>
        </div>
      </div>
    );
  }
}
