import { useState } from 'react';
import { Redirect } from 'react-router-dom';
import { generate } from 'canihazusername'

export default function Login({ logIn }) {
  const [username, setUsername] = useState(generate('{character} {english}'));
  const [loggedIn, setLoggedIn] = useState(false);

  if (loggedIn) {
    return (
      <Redirect to="/skychat/home" />
    );
  }

  return (
    <div id="login-page">
      <div id="login-card">
        <h2>Welcome To SkyChat</h2>
        <input
          type="text"
          className="form__field"
          placeholder="Username"
          name="name"
          id="name"
          onChange={(e) => setUsername(e.target.value)}
          value={username}
          required
        />
        <div className="login-button" onClick={() => {
          if (!username?.length) setUsername(generate('{character} {english}'));
          logIn(username);
          setLoggedIn(true);
        }}>
          Login
        </div>
      </div>
    </div>
  );
}
